/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;
  /*========== Colors ==========*/
  --first-color: #069C54;
  --first-color-alt: #048654;
  --title-color: #393939;
  --text-color: #707070;
  --text-color-light: #A6A6A6;
  --body-color: #FBFEFD;
  --container-color: #FFFFFF;

  /*========== Font and typography ==========*/
  --body-font: 'Poppins', sans-serif;
  --biggest-font-size: 2.25rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes ==========*/
  --mb-1: .5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}

@media screen and (min-width: 768px){
  :root{
    --biggest-font-size: 4rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/*========== BASE ==========*/
*,::before,::after{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

/*========== Variables Dark theme ==========*/
body.dark-theme{
  --title-color: #F1F3F2;
  --text-color: #C7D1CC;
  --body-color: #1D2521;
  --container-color: #27302C;
}

/*========== Button Dark/Light ==========*/
.change-theme{
  position: absolute;
  right: 1rem;
  top: 1.8rem;
  color: #707070;
  color: var(--text-color);
  font-size: 1rem;
  cursor: pointer;
}

body{
  margin: 3rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  background-color: #FBFEFD;
  background-color: var(--body-color);
  color: #707070;
  color: var(--text-color);
  line-height: 1.6;
}

h1,h2,h3,p,ul{
  margin: 0;
}

ul{
  padding: 0;
  list-style: none;
}

a{
  text-decoration: none;
}

img{
  max-width: 100%;
  height: auto;
}

/*========== CLASS CSS ==========*/
.section{
  padding: 4rem 0 2rem;
}

.section-title, .section-subtitle{
  text-align: center;
}
.section-title1
{
  text-align: center;
}
.section-title{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.section-subtitle{
  display: block;
  color: #069C54;
  color: var(--first-color);
  font-weight: 500;
  font-weight: var(--font-medium);
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

/*========== LAYOUT ==========*/
.bd-container{
  max-width: 960px;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-left: var(--mb-2);
  margin-right: 1rem;
  margin-right: var(--mb-2);
}

.bd-grid{
  display: grid;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.l-header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
  background-color: #FBFEFD;
  background-color: var(--body-color);
}

/*========== NAV ==========*/
.nav{
  max-width: 1100px;
  height: 3rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
}

/* @media screen and (max-width: 1024px) */
@media screen and (max-width: 768px) and (min-width: 401px)
{
  .nav__menu{
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    padding: 1.5rem 0 1rem;
    text-align: center;
    /* background-color: var(--body-color); */
    /* background-color: red; */
    transition: .4s;
    box-shadow: 0 4px 4px rgba(0,0,0,.1);
    border-radius: 0 0 1rem 1rem;
    z-index: 100;
    z-index: var(--z-fixed);
  }
}

.nav__item{
  /* margin-bottom: var(--mb-2); */
  /* font-size: 10px; */
  float: right;
  /* padding: 0 8px 0 8px; */
}

.nav__link, .nav__logo, .nav__toggle{
  color: #707070;
  color: var(--text-color);
  /* font-weight: var(--font-medium); */
}
.nav__logo
{
  font-size: 45px;
  color: #069C54;
  color: var(--first-color);
}
.nav__logo.nav__logos
{
  font-size: 10px;
  background-color: blueviolet;
}
.nav__logo:hover{
  color: #069C54;
  color: var(--first-color);
}

.nav__link{
  transition: .3s;
  font-size: 10px;
  flex-direction: column-reverse;
  
}

.nav__link:hover{
  color: #069C54;
  color: var(--first-color);
}

.nav__toggle{
  font-size: 1.3rem;
  cursor: pointer;
}

/* Show menu */
.show-menu{
  top: 3rem;
  top: var(--header-height);
}

/* Active menu */
.active-link{
  color: #069C54;
  color: var(--first-color);
}

/* Change background header */
.scroll-header{
  box-shadow: 0 2px 4px rgba(0,0,0,.1);
}

/* Scroll top */
.scrolltop{
  position: fixed;
  right: 1rem;
  bottom: -20%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: .3rem;
  background: rgba(6,156,84,.5);
  border-radius: .4rem;
  z-index: 10;
  z-index: var(--z-tooltip);
  transition: .4s;
  visibility: hidden;
}

.scrolltop:hover{
  background-color: #048654;
  background-color: var(--first-color-alt);
}

.scrolltop__icon{
  font-size: 1.8rem;
  color: #FBFEFD;
  color: var(--body-color);
}

/* Show scrolltop */
.show-scroll{
  visibility: visible;
  bottom: 1.5rem;
}

/*========== HOME ==========*/
.home__container{
  height: calc(100vh - 3rem);
  height: calc(100vh - var(--header-height));
  align-content: center;
}

.home__title{
  /* font-size: var(--biggest-font-size); */
  font-size: 50px;
  /* color: var(--first-color); */
  color: black;
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.home__subtitle{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  color: #393939;
  color: var(--title-color);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.home__img{
  width: 300px;
  justify-self: center;
}

/*========== BUTTONS ==========*/
.button{
  display: inline-block;
  background-color: #069C54;
  background-color: var(--first-color);
  color: #FFF;
  padding: .75rem 1rem;
  border-radius: .5rem;
  transition: .3s;
}

.button:hover{
  background-color: #048654;
  background-color: var(--first-color-alt);
}

/*========== ABOUT ==========*/
.about__data{
  text-align: center;
}

.about__description{
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

.about__img{
  width: 280px;
  border-radius: .5rem;
  justify-self: center;
}

/*========== SERVICES ==========*/
.services__container{
  grid-row-gap: 2.5rem;
  row-gap: 2.5rem;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.services__content{
  text-align: center;
}

.services__img{
  width: 64px;
  height: 64px;
  fill: #069C54;
  fill: var(--first-color);
  background-color: blue;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.services__title{
  /* font-size: var(--h3-font-size); */
  font-family: sans-serif;
  font-size: 167px;
  color: #0A5169;
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.services__description{
  padding: 0 1.5rem;
}

/*========== MENU ==========*/
.menu__container{
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
}

.menu__content{
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  background-color: var(--container-color);
  border-radius: .5rem;
  box-shadow: 0 2px 4px rgba(3,74,40,.15);
  padding: .75rem;
}

.menu__img{
  width: 100px;
  align-self: center;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-2);
}

.menu__name, .menu__preci{
  font-weight: 600;
  font-weight: var(--font-semi-bold);
  color: #393939;
  color: var(--title-color);
}

.menu__name{
  font-size: .938rem;
  font-size: var(--normal-font-size);
}

.menu__detail, .menu__preci{
  font-size: .813rem;
  font-size: var(--small-font-size);
}

.menu__detail{
  margin-bottom: .5rem;
  margin-bottom: var(--mb-1);
}

.menu__button{
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  padding: .625rem .813rem;
  border-radius: .5rem 0 .5rem 0;
}

/*========== APP ==========*/
.app__data{
  text-align: center;
}

.app__description{
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-5);
}

.app__stores{
  margin-bottom: 2rem;
  margin-bottom: var(--mb-4);
}

.app__store{
  width: 120px;
  margin: 0 .5rem;
  margin: 0 var(--mb-1);
}

.app__img{
  width: 230px;
  justify-self: center;
}

/*========== CONTACT ==========*/
.contact__container{
  text-align: center;
}

.contact__description{
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-3);
}

/*========== FOOTER ==========*/



/*========== MEDIA QUERIES ==========*/
@media screen and (min-width: 576px){
  .home__container,
  .about__container,
  .app__container{
    grid-template-columns: repeat(2,1fr);
    align-items: center;
  }

  .about__data, .about__initial,
  .app__data, .app__initial,
  .contact__container, .contact__initial{
    text-align: left;
    text-align: initial;
  }

  .about__img, .app__img{
    width: 380px;
    order: -1;
  }

}
@media screen and (min-width: 768px){
  body{
    margin: 0;
  }

  .section{
    padding-top: 8rem;
  }

  .nav{
    height: calc(3rem + 1.5rem);
    height: calc(var(--header-height) + 1.5rem);
  }
  .nav__list{
    display: flex;
  }
  .nav__item{
    margin-left: 2.5rem;
    margin-left: var(--mb-5);
    margin-bottom: 0;
  }
  .nav__toggle{
    display: none;
  }

  .change-theme{
    position: static;
    position: initial;
    margin-left: 1rem;
    margin-left: var(--mb-2);
  }

  .home__container{
    height: 100vh;
    justify-items: center;
  }

  .services__container,
  .menu__container{
    margin-top: 3rem;
    margin-top: var(--mb-6);
  }

  .menu__container{
    grid-template-columns: repeat(3, 210px);
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
  }
  .menu__content{
    padding: 1.5rem;
  }
  .menu__img{
    width: 130px;
  }

  .app__store{
    margin: 0 .5rem 0 0;
    margin: 0 var(--mb-1) 0 0;
  }
}

@media screen and (min-width: 960px){
  .bd-container{
    margin-left: auto;
    margin-right: auto;
  }

  .home__img{
    width: 500px;
  }

  .about__container,
  .app__container{
    grid-column-gap: 7rem;
    -webkit-column-gap: 7rem;
            column-gap: 7rem;
  }
}
